$main-scout-color: #5793CE;
$highlight-secondary: #F8CE46;
$highlight-tertiary: #2C4678;

$tablet: 780px;
$small-phone: 320px;

@import url('https://fonts.googleapis.com/css?family=Montserrat:300,300i,400,400i,700,700i&display=swap');
@import "bootstrap/bootstrap";
@import url('https://netdna.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css');


p {
    margin: 0;
}

h1, h2, h3, h4, h5, h6 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: normal;
}

// MODULES
@import "_modules/home";
@import "_modules/navbar";
@import "_modules/content";
@import "_modules/text";
@import "_modules/header";
@import "_modules/footer";
@import "_modules/link";
@import "_modules/btn";
@import "_modules/card";
@import "_modules/form";
@import "_modules/breadcrumb";
@import "_modules/carousel";
@import "_modules/accordion";
@import "_modules/banner";
@import "_modules/convenio";
@import "_modules/badge";
@import "_modules/pagination";
@import "_modules/social";
@import "_modules/search";
@import "_modules/map";
@import "_modules/materia";
@import "_modules/sidebar";
@import "_modules/faq";

body {
    font-family: 'Montserrat', sans-serif;
    @media all and (max-width:$tablet){
        padding: 60px 0 0;
    }
}

.no-overflow {
    overflow: hidden;
}

.remove-padding-mobile {
    @media all and (max-width: $tablet){
        padding: 0;
    }  
}

.column-reverse-mobile {
    @media all and (max-width: $tablet){
        flex-direction: column-reverse !important;
    }   
}

.mb-mobile-5 {
    @media all and (max-width: $tablet){
        margin-bottom: 3rem !important;
    }
}

/////
.download-menu-wrapper {
    overflow: hidden;
    
    .btn {
        overflow: hidden;
        float: left;
        display: block;
        width: 45%;
        margin: 0 5% 0 0;

        white-space: nowrap;
        text-overflow: ellipsis;

        @media all and (max-width: $tablet){
            float: none;
            width: 100%;
            margin: 0;
        }
    }
    
    &--three-columns {
        .btn {
            width: 30.33%;
            margin: 0 3% 0 0;

            @media all and (max-width: $tablet){
                float: left;
                display: flex;
                justify-content: center;
                flex-direction: column;
                width: 47%;
                height: 70px;
                margin: 0 3% 0 0;
                padding: .5rem;
                text-overflow: unset;
                white-space: normal;
                font-size: .95rem;
            }
        }
    }
}